import React from 'react';
import Fade from 'react-reveal/Fade';
import { Container } from 'react-bootstrap';
import Title from '../components/Title';

const About = ({ isDesktop, isMobile }) => {
  return (
    <section id="about">
      <Container>
        {isDesktop && (
          <>
            <Title title={'Yuki Nishidate / '} />
            <Fade right duration={500} delay={250} distance="30px">
              <p>{''}</p>
              <br></br>
            </Fade>

            <Fade right duration={500} delay={350} distance="30px">
              <h2>{''}</h2>
              <p>{'  '}</p>
              <p>{'    '}</p>
              <br></br>
            </Fade>

            <Fade right duration={500} delay={450} distance="30px">
              <h2>{''}</h2>
              <p>{' 3D'}</p>
              <p>{' R&D'}</p>
              <br></br>
            </Fade>

            {/* <Fade right duration={500} delay={550} distance="30px">
              <h2>{''}</h2>
              <p>{' DHU U18'}</p>
              <p>{' CG'}</p>
              <p>{' ...'}</p>
            </Fade> */}
          </>
        )}
        {isMobile && (
          <>
            <Title title="Yuki Nishidate" />
            <p>
              {
                '  '
              }
            </p>
          </>
        )}
      </Container>
    </section>
  );
};

export default About;
